.catalog {
	padding: rem(10) 0 rem(64) 0;

	&__header.scroll-active {
		top: rem(75);
	}

	&__right {
		padding-left: rem(34);

		.product_item {
			width: calc((100% - 4rem) / 3);


		}
	}
}

.product_item {
	margin-bottom: rem(32);

	&__text {
		padding: 0 rem(20);
	}

	&__subcat {
		font-size: rem(12);
	}
}