.sertifications_accordion {
	position: relative;
	padding: 0;

	

	>.container {
		display: grid;
		width: auto;
		grid-template-columns: calc((100% - 1220px)/2) 6fr 4fr calc((100% - 1220px)/2);
	}

	.w-7\/12 {
		// flex: 0 0 65%;
		// max-width: 65%;
		// flex: 0 0 rem(587);
		// max-width: rem(587);
	}

	.w-5\/12 {
		flex: none;
		max-width: 100%;
	}

	&__left {
		grid-column: 2 / span 1;
		// flex: 0 0 rem(589);
		// max-width: rem(589);
		// padding-top: rem(85);
		// padding-left: rem(84);
		// -right: rem(30);

		padding: rem(85) 0 rem(30) 0;
		margin-right: rem(30);
		margin-bottom: rem(120);
		h2 {
			display: block;
			font-size: rem(34);
			font-weight: 500;
			padding-bottom: rem(62);
			border-bottom: rem(1) solid var(--c-dark);
			letter-spacing: rem(0.5);
		}

		
	}

	&__right {
		grid-column-end: span 2;
		display: block;
		overflow: hidden;
		// flex-grow: 1;
		
		// position: absolute;
		// top: 0;
		// right: 0;
		// width: 100%;
		// height: 100%;

		// max-height: rem(1041);

		

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

.accordion {
	&__item {
		.title {
			span {
				font-size: rem(15);
			}
		}

		.text {

			p {
				font-size: rem(15);
			}
		}
	}
}