.other_product {
	padding: rem(84) 0;

	>.container {
		display: flex;
		flex-direction: column;
	}

	.swiper-nav {
		display: none;
	}

	.swiper-wrapper {
		// display: flex;
		flex-wrap: wrap;

		justify-content: space-between;

		.swiper-slide {
			width: calc((100% - 1.5rem) / 4);
		}
	}


	h3 {
		font-size: rem(36);
	}

	p {
		max-width: rem(384);
		margin-bottom: rem(56);
	}

	.product_item {
		padding: 0;

