		// 		width: rem(220);
		// 		height: rem(220);
		// 		margin-left: rem(-110);
		// 		top: 0;
		// 	}
		// }

		&__text {
			h3 {
				font-size: rem(18);

				max-width: rem(210);
				margin-left: auto;
				margin-right: auto;
			}
		}
	}

	.button_primary {
		margin-top: rem(56);
		align-self: center;
	}
}