.reviews_col {
	// background-color: var(--c-accent);
	padding: rem(78) 0 rem(62) 0;
	// background-image: none;
	// background-size: cover;

	>.container {
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
	}

	&__left {
		width: rem(400);
		margin-bottom: 0;

		h3 {
			// font-family: $font-merri;
			// font-size: rem(36);
			// line-height: 150%;
			// font-weight: normal;
			margin-bottom: rem(35);

			text-align: left;
		}

		.links {
			// display: flex;
			flex-direction: column;
			a {
				align-self: flex-start;
				font-size: rem(15);

				margin-bottom: rem(20);

				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}

	&__right {
		// margin-left: rem(30);

		width: rem(800);

		.swiper-nav {
			margin-top: rem(34);
			height: rem(38);
		}
	}
}