.questions {
	padding: rem(100) 0;

	> .container {
		display: flex;
		flex-wrap: wrap;
	}

	&__left {
		flex: 0 0 50%;
		max-width: 50%;
		margin-bottom: 0;
	}

	&__right {
		flex: 0 0 50%;
		max-width: 50%;
		
		.accordion {

			&__item {

				.title {

					span {
						font-size: rem(15);
					}
				}
			}
		}
	}
}