.product_header_fixed {
	height: rem(82);

	flex-wrap: wrap;
	flex-direction: row;

	&__left {
		display: flex;
		flex-wrap: nowrap;

		.image {
			display: block;
			margin-right: rem(5);

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.title {
			text-align: left;
			align-items: center;

			span {
				font-size: rem(22);
			}
		}
	}

	&__right {
		display: flex;
		flex-wrap: wrap;
		// align-items: center;

		ul {
			display: block;
			margin-right: rem(24);
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			li {
				margin-right: rem(37);

				&:last-child {
					margin-right: 0;
				}

				a {
					font-size: rem(17);
					line-height: 150%;
					font-weight: 500;
					color: black;
				}
			}
		}

		>a {
			width: auto;
			background-color: var(--c-accent);
			display: flex;
			align-items: center;
			justify-content: center;

			padding: 0 rem(78);

			height: rem(82);

			font-weight: 500;
			font-size: rem(16);
			color: white;
		}
	}
}